<template>
  <!-- <img alt="Vue logo" src="./assets/logo.png"> -->
  <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->
  <div class="main">

  <div class="sel">
    <div class="sel-head">
    <h4>请在此选择一个测试程序</h4>   
   </div>
    <el-table
        max-height="430"
        min-height
        ref="singleTable"
        :data="tabledata"
        highlight-current-row
        @current-change="handleCurrentChange"
        style="width: 100%">
        <el-table-column
          label="序号"
          type="index"
          width="40">
        </el-table-column>
        <el-table-column
          property="name"
          label="发布人"
          width="100">
        </el-table-column>
        <el-table-column
          property="msg"
          label="留言"
          width="210">
        </el-table-column>
    </el-table>
    <el-button style="margin-left: 10px;" size="small" type="success" @click="handleData">上传你的程序</el-button>
  </div>

  <div class="form">
    <h1 class="header">
      Check Your Program With US!
    </h1>
    <el-form ref="form" :model="send" label-width="120px">
      <el-form-item label="编码文本">
        <el-input v-model="send.content" type="textarea" rows="7"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="encodeSubmit">Encode</el-button>
        <el-button type="warning" @click="decodeSubmit">Decode</el-button>
      </el-form-item>
      <el-form-item label="解码文本">
        <el-input v-model="receive.content" type="textarea" rows="7"></el-input>
      </el-form-item>
    </el-form>
    </div>
  </div>

  <el-dialog
    v-model="dialogVisible"
    title="ExtraData"
    width="30%"
  >
    <el-input v-model="extraData.name" placeholder="Please input Your Name"  maxlength="20"/>
    <div style="margin: 5px 0"></div>
    <el-input v-model="extraData.msg" placeholder="Please Leave a Message for Contacting" maxlength="200"/>

    <template #footer>
      <span class="dialog-footer"> 
        <el-upload
        ref="upload"
        action="/rec/addprog"
        :data="extraData"
        :limit="1"
        :auto-upload="false"
        :on-success="upLoadSuccess"
        :on-error="upLoadFailed"
        >
          <template #trigger>
            <el-button size="small" type="primary">选择文件</el-button>
          </template>
          <el-button
            style="margin-left: 10px"
            size="small"
            type="success"
            @click="submitUpload"
            >提交程序</el-button>    
        </el-upload>  
      </span>
    </template>
  </el-dialog>
</template>

<script>
import axios from 'axios'
import { ElMessage } from 'element-plus'

axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
axios.defaults.headers.get['Content-Type'] = 'application/x-www-form-urlencoded';
axios.defaults.transformRequest = [function (data) {
    let ret = ''
    for (let it in data) {
      ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
    }
    return ret
}]

export default {
  name: 'App',
  components: {
  //  HelloWorld
  },
  data(){
    return {
      dialogVisible:false,
      tabledata : [

      ],
      send: {
        id : '0',
        type : 'encode',
        content : ''
      },
      receive : {
        id : '0',
        type : 'decode',
        content: ''
      },
      fileList: [
      ],
      extraData :{
        name:'',
        msg:''
      }
    }
    },
  mounted(){
      axios({
        method:'get',
        url:'/rec/getprog'
      }).then((response) =>{
        let retdata = response.data.retlist
        retdata.forEach((val)=>{
          this.tabledata.push(val);
        })
      })  
    },
  
  methods: {
    encodeSubmit() {
      axios({
        method: 'post',
        url:'/rec/run',
        data : this.send,
      })
      .then((response)=>{
          if(response.data.ret == 0) {
            this.receive.content = response.data.content
          }else {
            this.receive.content = response.data.msg
          }
      })   
      .catch(()=>{
          ElMessage({
            showClose: true,
            message: 'Network Error',
            type: 'error',
            duration : 1500
          })        
      })
    },
    decodeSubmit() {
      axios({
        method: 'post',
        url:'/rec/run',
        data : this.receive,
      })
      .then((response)=>{
          if(response.data.ret == 0) {
            this.send.content = response.data.content
          }else {
            this.send.content = response.data.msg
          }      
      })
      .catch(()=>{
          ElMessage({
            showClose: true,
            message: 'Network Error',
            type: 'error',
            duration : 1500
          })        
      })
    },
    handleCurrentChange(val) {
      this.receive.id = this.send.id = val.id;
    },
    handleData() {
      this.dialogVisible = true;
    },
    submitUpload() {
      if(this.extraData.name === '' || this.extraData.msg === '') {
        ElMessage({
          showClose: true,
          message: 'Oops, your extra data is empty.',
          type: 'error',
          duration : 1500
        })
      }else {
        this.$refs.upload.submit()
      }
    },
    upLoadSuccess() {
      ElMessage({
        showClose: true,
        message: 'Fine.Please Press F5 to Refesh',
        type: 'success',

      })
      location.reload() ;  
    },
    upLoadFailed(){
      ElMessage({
        showClose: true,
        message: 'Oop!Err Occured!Try again',
        type: 'error',
        duration : 1500
      })
      location.reload() ;    
    }
  },
}
</script>

